@import '../../style/variables.scss';

.episodes {
    position: relative;
    margin: 0 auto;
    max-width: 1200px;
    color: #fff;
    text-align: center;

    &__title {
        margin-top: 30px;
        font-size: 50px;
        font-weight: 900;
    }

    &__item {
        margin-top: 45px;
        font-size: 25px;
        font-weight: 500;
    }

    &__name {
        display: block;
        font-size: 30px;
        font-weight: 700;
        transition: .6s all;

        &:hover {
            color: $second-color;
        }
    }

    &__date {
        font-size: 15px;
        font-weight: 400;
    }

    &__img {
        margin-top: 20px;
        width: 100%;
        height: 100%;
        display: grid;
        grid-template-columns: repeat(auto-fit, 100px);
        justify-content: center;
        gap: 10px;
        z-index: -1;

        img {
            position: relative;
            display: block;
            width: 100px;
            height: 100px;
            object-fit: cover;
            filter: grayscale(50%);
            transition: .6s all;


            &:hover {
                transform: scale(1.5);
                filter: saturate(200%);
                box-shadow: 5px 5px 35px rgba(0, 0, 0, .9);
                z-index: 5;
            }
        }
    }
}